canvas {

    position: fixed;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 100%;
    height: 100%;
    z-index: -1;
    
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;

}